.card-img-top {
  transition-duration: .5s;
}
card, .card-img-top:hover {
  opacity: 0.3;
  position: relative;

}
.card-body {
  position: absolute;
  background: rgb(237,232,238);
  background: radial-gradient(circle, rgba(237,232,238,1) 61%, rgba(181,192,235,1) 90%);
  top: 0;
  left: 0;
  // margin: 10px;
  z-index: 1;
  opacity: .8;
  width: 100%;
  height: 100%;
  color: black;
  display: grid;
  place-content: center;
}
p.card-text {
  overflow: hidden;
  max-height: 120px;
}
div.card-footer {
  margin: 2em auto 0 0;
  padding: 0;
}
.card-footer {
  border: none;
  background: none;
}
.card-footer button.btn {
  padding: 0;
  margin: 0;
}